import 'whatwg-fetch'
import config from '../config'

let env = process.env.REACT_APP_ENV || 'development'
const handleJSONResponse =  (response: any) => {
  return response.json()
    .then((json: JSON) => {
      if (response.ok) {
        return json
      } else {
        return Promise.reject(Object.assign({}, json, {
          status: response.status,
          statusText: response.statusText
        }))
      }
    })
}
const handleTextResponse = (response: any) => {
  return response.text()
    .then((text: string) => {
      if (response.ok) {
        return text
      } else {
        return Promise.reject({
          status: response.status,
          statusText: response.statusText,
          err: text
        })
      }
    })
}

export let api: string = config[env].apiDomain

export const dataToString = (data: object, method: string) => {
  if (method === 'GET') {
    const array: any = []
    let index = 0
    for (const item in data) {
      if (data[item]) {
        array[index++] = [item, data[item]]
      }
    }
    return new URLSearchParams(array).toString()
  }
  return JSON.stringify(data)
}

export const fetchAPI = (
  url: string,
  data?: object,
  method: string = 'GET'
): Promise<Response> => {
  url = api + url
  let dataStr = ''
  if (data) {
    dataStr = dataToString(data, method)
  }
  let initObj: any = {
    headers: {
      Accept: 'application/json',
      'Content-Type': 'application/json'
    },
    method,
  }
  if (method === 'GET') {
    if (data) {
      if (url.indexOf('?') > -1) {
        url += '&' + dataStr
      } else {
        url += '?' + dataStr
      }
    }
  } else {
    initObj.body = dataStr
  }
  return new Promise((resolve, reject) => {
    fetch(url, initObj)
    .then(response => {
      let contentType: string = response.headers.get('content-type') || ''
      if (contentType.includes('application/json')) {
        return handleJSONResponse(response)
      } else if (contentType.includes('text/html')) {
        return handleTextResponse(response)
      } else {
        // Other response types as necessary. I haven't found a need for them yet though.
        throw new Error(`Sorry, content-type ${contentType} not supported`)
      }
    })
    .then(response => resolve(response))
    .catch(err => reject(err))
  })
}



